.production {
  position: relative;

  display: inline-block;

  margin-right: 20px;
}

.production:last-child {
  margin-right: 0;
}

.production h3 {
  position: absolute;
  top: 20px;
  left: 20px;

  font-size: 12px;
  line-height: 30px;
  color: #ffffff;
}

.production img {
  display: block;

  opacity: 0.2;
}

.production svg {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;

  padding: 15px 20px;
  width: 6px;
  height: 5px;

  background-color: #00b2ff;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .production {
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 20px;
  }

  .production img {
    width: 500px;
    height: 315px;
  }
}

@media (min-width: 320px) and (max-width: 767px) {
  .production {
    margin: 0;
    margin-bottom: 20px;
  }

  .production img {
    width: 300px;
    height: 300px;
  }
}
