body {
  margin: 0;
  padding: 0;

  font-family: "Roboto", "Helvetica", sans-serif;
  font-size: 14px;

  letter-spacing: 0;
  line-height: 21px;
  box-sizing: border-box;
}

/*временно для подгона под макет*/
body:after {
  content: '';
  display: block;
  width: 100%;
  height: 10000px;
  position: absolute;
  background-image: url('../img/SD1024.jpg');
  background-repeat: no-repeat;
  background-position: center top;
  opacity: 0.8;
  pointer-events: none;
  top: 0;

  @media (min-width: 768px) and (max-width: 1023px) {
    background-image: url('../img/SD1024.jpg');
  }

  @media (min-width: 320px) and (max-width: 767px) {
    background-image: url('../img/SD1024.jpg');
  }
}
